body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.textbox{
  border: 1px black solid;
  padding-left: 3px;
}

div.label{
  float: right;
}

.row {
  margin-top: 10px;
}

.textbox.address,
.textbox.long-description{
  height: 80px;
}

.load-items .container.seller-info{
  margin-top: 30px;
}

table.reviews-result td,
table.reviews-result tr
{
  border: 1px grey solid !important;
  text-align: center
}

table.reviews-result th{
  border: 1px grey solid !important;
  text-align: center;
  background-color: lightgrey
}

.reviews-result-dialog {
  max-width: 60% !important;
}

.reviews-result-dialog .container.seller-info{
  padding: 0;
}


.item-details .horizontal-container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-align-content: stretch;
          align-content: stretch;
}

.item-details .header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 10px;
  /* font-size: 20px; */
}

.item-details.vertical-container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-align-content: stretch;
          align-content: stretch;
  height: 100%;
}

.item-details .horizontal-container > .segment.grow.lg{
  -webkit-flex-grow: 1.1;
          flex-grow: 1.1;
}

.item-details .horizontal-container > .segment.grow.sm{
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.item-details .horizontal-container > .segment.lg{
  width: 70%;
}

.item-details .horizontal-container > .segment.sm{
  width: 30%;
}

.item-details .horizontal-container.grow{
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.item-details .horizontal-container > .segment {
  border: black solid 1px;
  border-radius: 10px;
  margin: 10px;
}

.item-details .status-label {
  margin-left: 30px;
}

.item-details .no-header .tabulator-header {
  height: 0;
}

.item-details .tabulator-table {
  overflow: auto;
}

.item-details .images-table .tabulator-table .product-image{
  height: 200px;
  width: 200px;
}

.item-details .images-table .tabulator-table .tabulator-cell{
  height: 200px;
}

.item-details .item-url {
  overflow-wrap: break-word;
}
.page-header{
    text-align: center;
    margin-bottom: 20px;
}

.tabulator .tabulator-header .tabulator-col {
    text-align: center;
    background-color: white;
}

.tabulator .tabulator-header .tabulator-col .tabulator-col-content {
    height: 60px;
}

.tabulator .tabulator-header .tabulator-col .tabulator-col-content .tabulator-col-title {
    white-space: normal;
}

.tabulator .tabulator-row.tabulator-row-even {
    background-color: white;
}

.tabulator .tabulator-row:hover {
    background-color: white;
}

.tabulator .tabulator-row .tabulator-cell{
    white-space: normal;
}

.tabulator .tabulator-row {
    border-top: grey 1px solid;
}

.tabulator .tabulator-header,
.tabulator .tabulator-headers{
    border-bottom-style: none;
}

.ico{
  font-size: 25px;
}

.ico-approved{
  color: green;
}

.ico-rejected{
  color: red;
}

.ico-escalated{
  color: orange;
}

.filter-status-choice .row{
  margin-top: 0;
}

.filter-status-choice [class^="col-"]{
  padding: 0;
}
main {
  height: 90vh;
}

button.btn.btn-secondary {
  background-color: white;
  color: black;
}

button.btn.btn-secondary:hover {
  background-color: lightgrey;
}
